
@import url('https://fonts.googleapis.com/css2?family=Jost:wght@300;400;700&family=Kanit:ital,wght@1,300&display=swap');

@media screen and (min-width: 320px) {

    @keyframes slideInLeft {
        0%{position: absolute; right: -30vw;}
        100%{position: absolute; right: 0;}
    }

.navbar {
    display: flex;
    flex-direction: row;
    justify-content:end;
    width: 80vw;
    margin-top: 2rem;
    padding-left: 10vw;
    font-size: min(1.5rem,4vw);
    font-weight: bold;
    color: white;
    position: fixed;
    top: 0;
    animation-delay: 2s;
    z-index: 999;

    &__link{
        color: whitesmoke;
        text-decoration: none;
        position: absolute;
        right: -30vw;
        animation: slideInLeft 0.2s ease forwards;
        animation-delay: 1s;
        &-snap{
            position: absolute;
            left:200%;
            &:visited {
            color: whitesmoke;
        }
        }
        
        &:visited {
            color: whitesmoke;
        }
        
        &:hover{
            color: whitesmoke;
        }
    }
}

}

@media screen and (min-width: 768px) {

    .navbar{
        &__link-snap{
            left: 140%;
        }
    }
}

@media screen and (min-width: 1024px) {

    .navbar{
        &__link-snap{
            left: 120%;
        }
    }
}